import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { IAlertConfig } from '@shared/models/alert';
import { finalize, Subject, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { IResponse } from '@shared/models/response-with-token';
import { FacturacionService } from '../../services/facturacion.service';
@Component({
    selector: 'app-calcular-unitario',
    templateUrl: './calcular-unitario.component.html',
    styleUrls: ['./calcular-unitario.component.scss']
})
export class CalcularUnitarioComponent implements OnInit {
    columns = ['nombre', 'activo', 'acciones'];
    form: FormGroup;
    title: string;
    showAlert = false;
    _alertConfig: IAlertConfig;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    @Output() public creadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    @Output() public editadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    deposito_default = 0;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _empresaService: EmpresaService,
        private alert: AppAlertService,
        private facturacionService: FacturacionService,
        public dialogRef: MatDialogRef<CalcularUnitarioComponent>
    ) {
        this.title = `Calcular Precio Unitario ${this.data.descripcion}`;

        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
                this.deposito_default = empresa.deposito_default;
            });
    }

    ngOnInit(): void {
        // Load empresa data
        this._empresaService.getEmpresa();
        this.newForm();
        this.subscribeAlert();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1500)
                    .pipe(
                        finalize(() => {
                            this.showAlert = false;
                            this._alertConfig = null;
                        }),
                        takeWhile(() => time > 0),
                        takeUntil(this._unsubscribeAll),
                        tap(() => (time -= 1))
                    )
                    .subscribe();
            },
        });
    }

    newForm() {
        this.form = new FormGroup({
            articulo: new FormControl({ value: `${this.data.codigo_interno} | ${this.data.descripcion}`, disabled: true }, Validators.required),
            bruto: new FormControl((''), Validators.required),
        });
    }

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.error('Revise Los Datos Ingresados');
        } else {
            const payload = {
                idarticulo: this.data.idarticulo,
                bruto: this.form.get('bruto').value
            }

            this.facturacionService.calcularPrecioUnitario(payload)
                .subscribe({
                    next: (data: IResponse<any>) => {
                        if (data) {
                            this.creadoExitosamente.emit();
                            this.onCloseMenu(data);
                        }
                    }
                })
        }
    }

    onCloseMenu(data) {
        this.dialogRef.close(data)
    }
}