import { NgModule } from '@angular/core';
import { CommonModule, DatePipe } from '@angular/common';
import { GuardarFacturaComponent } from './components/guardar-factura/guardar-factura.component';
import { Route, RouterModule } from '@angular/router';
import { ReactiveFormsModule } from '@angular/forms';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { MatButtonModule } from '@angular/material/button';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatDialogModule } from '@angular/material/dialog';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatInputModule } from '@angular/material/input';
import { MatRadioModule } from '@angular/material/radio';
import { MatSelectModule } from '@angular/material/select';
import { MatSortModule } from '@angular/material/sort';
import { MatTableModule } from '@angular/material/table';
import { MatToolbarModule } from '@angular/material/toolbar';
import { MatTooltipModule } from '@angular/material/tooltip';
import { FuseAlertComponent } from '@fuse/components/alert';
import { TablaDinamicaModule } from '@shared/components/tabla-dinamica/tabla-dinamica.module';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { FacturacionService } from './services/facturacion.service';
import { FacturasComponent } from './components/facturas.component';
import { CalcularUnitarioComponent } from './components/calcular-unitario/calcular-unitario.component';
import { DialogAccionesComponent } from './components/dialog-acciones-factura/dialog-acciones/dialog-acciones.component';

const routes: Route[] = [
    { path: '', component: FacturasComponent },
    { path: 'guardar-factura/:id', component: GuardarFacturaComponent },
    { path: 'nueva-factura', component: GuardarFacturaComponent },
];

@NgModule({
    declarations: [
        FacturasComponent,
        GuardarFacturaComponent,
        CalcularUnitarioComponent,
        DialogAccionesComponent,
    ],
    imports: [
        CommonModule,
        RouterModule.forChild(routes),
        MatTableModule,
        MatButtonModule,
        MatSelectModule,
        MatFormFieldModule,
        ReactiveFormsModule,
        MatInputModule,
        MatIconModule,
        FuseAlertComponent,
        MatTooltipModule,
        MatAutocompleteModule,
        MatToolbarModule,
        MatCheckboxModule,
        MatSortModule,
        TablaDinamicaModule,
        MatDialogModule,
        MatRadioModule,
        MatDatepickerModule,
    ],
    exports: [RouterModule],
    providers: [
        JedsMenuService,
        AppAlertService,
        DatePipe,
        FuseConfirmationService,
        FacturacionService,
    ],
})
export class FacturacionModule {}
