import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Router } from '@angular/router';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { EmpresaService } from '@shared/service/app/empresa.service';
import {
    debounceTime,
    distinctUntilChanged,
    startWith,
    Subject,
    takeUntil,
} from 'rxjs';
import { FacturacionService } from '../../services/facturacion.service';
import { FormControl, FormGroup } from '@angular/forms';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { AppAlertService } from '@shared/service/app/alert.service';
import { MatDialog } from '@angular/material/dialog';
import { CalcularUnitarioComponent } from '../calcular-unitario/calcular-unitario.component';

const MY_DATE_FORMATS = {
    parse: {
        dateInput: 'mm/dd/yyyy',
    },
    display: {
        dateInput: 'dd/MM/yyyy',
        monthYearLabel: 'MMM yyyy',
        dateA11yLabel: 'LL',
        monthYearA11yLabel: 'MMMM yyyy',
    },
};

@Component({
    selector: 'app-guardar-factura',
    templateUrl: './guardar-factura.component.html',
    styleUrls: ['./guardar-factura.component.scss'],
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }],
})
export class GuardarFacturaComponent implements OnInit, OnDestroy {
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    fechaActual: Date = new Date();

    spinner: boolean = false;

    subtotal: number;
    faltaFacturarVista;
    montoFaltaFacturar;

    clienteCobro;
    clientes: [] = [];
    clientesSub;
    puntosVenta: [] = [];
    comprobantes;
    comprobanteComboSub;
    comprobantesAsociados: [] = [];
    ventasAsociadas: [] = [];
    facturaForm: FormGroup;

    articulosSub;
    articulos: [] = [];

    facturaDataSource = new MatTableDataSource<any>([
        {
            idarticulo: '',
            cantidad: '',
            importe_unitario: '',
            detalle: '',
        },
        {
            idarticulo: '',
            cantidad: '',
            importe_unitario: '',
            detalle: '',
        },
        {
            idarticulo: '',
            cantidad: '',
            importe_unitario: '',
            detalle: '',
        },
    ]);

    cantidadSub;
    precioUnitarioSub;
    desdeCobranza: boolean;
    idFactura: number;
    factura;
    cobro = null;
    idCobro: number;
    idcliente: number;
    columns = ['detalle', 'cantidad', 'precio_unitario', 'total', 'acciones'];
    constructor(
        private router: Router,
        private route: ActivatedRoute,
        private textService: HeaderTextService,
        private empresaService: EmpresaService,
        private facturacionService: FacturacionService,
        private alert: AppAlertService,
        private dialog: MatDialog
    ) {
        this.textService.setTitulo(this.router);
        if (this.router.url.split('/').includes('guardar-factura')) {
            this.idCobro = Number(this.router.url.split('/')[3].split('?')[0]);
        } else {
            this.idCobro = null;
        }
    }

    ngOnInit(): void {
        this.empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe((empresa) => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

            if(this.idCobro){
                this.getDatosCobro().then((res) => {
                    this.getData().then((res) => {
                        this.newForm();
                    });
                });
            } else {
                this.newForm();
                this.getData();
            }
    }

    initObservadores() {
        this.cantidadSub = this.facturaForm
            .get('cantidad')
            .valueChanges.subscribe((value) => {
                if (value > 0) {
                    this.calcularImportes();
                } else {
                    this.alert.error('La cantidad no puede ser menor a 1');
                }
            });
        this.precioUnitarioSub = this.facturaForm
            .get('precioUnitario')
            .valueChanges.subscribe((value) => {
                if (value > 0) {
                    this.calcularImportes();
                } else {
                    this.alert.error(
                        'El precio unitario no puede ser menor a 1'
                    );
                }
            });

        this.articulosSub = this.facturaForm
            .get('articulo')
            .valueChanges.pipe(
                distinctUntilChanged(),
                debounceTime(350),
                startWith(' ')
            )
            .subscribe((value) => {
                if (typeof value == 'string') {
                    this.getArticulos(value);
                }
            });

        this.clientesSub = this.facturaForm
            .get('cliente')
            .valueChanges.pipe(
                distinctUntilChanged(),
                debounceTime(350),
                startWith(' ')
            )
            .subscribe((value) => {
                if (typeof value == 'string') {
                    this.getClientes(value);
                } else {
                    this.idcliente = value.codigo;
                    this.getVentasAsociadas();
                }
            });

        this.comprobanteComboSub = this.facturaForm
            .get('comprobante')
            .valueChanges.subscribe((value) => {
                let isFactura = value.Desc.toLowerCase()
                    .split(' ')
                    .includes('factura');
                if (isFactura == true) {
                    this.facturaForm.get('comprobanteAsociado').setValue('');
                    this.facturaForm.get('comprobanteAsociado').disable();
                } else {
                    let isCredito = value.Desc.toLowerCase()
                        .normalize('NFD')
                        .replace(/[\u0300-\u036f]/g, '')
                        .split(' ')
                        .includes('credito');
                    let isDebito = value.Desc.toLowerCase()
                        .normalize('NFD')
                        .replace(/[\u0300-\u036f]/g, '')
                        .split(' ')
                        .includes('debito');
                    if (isCredito == true) {
                        let filter: string = `?filter[cliente]=${this.idcliente}&filter[generada]=1&filter[comprobante_asociado]=false`;
                        this.getComprobantesAsociados(filter);
                    }

                    if (isDebito == true) {
                        let filter: string = `?filter[cliente]=${this.idcliente}&filter[generada]=1&filter[comprobante_asociado]=true`;
                        this.getComprobantesAsociados(filter);
                    }
                    this.facturaForm.get('comprobanteAsociado').setValue('');
                    this.facturaForm.get('comprobanteAsociado').enable();
                }
            });
    }

    async getData(): Promise<void> {
        try {
            const resultados = await Promise.all([
                this.getClientes(),
                this.getDatosEmpresa(),
                this.getArticulos(),
            ]);
        } catch (error) {
            this.alert.error('Ocurri√≥ un error al cargar los datos');
        }
    }

    newForm() {
        this.facturaForm = new FormGroup({
            fecha: new FormControl(this.fechaActual),
            vencimientoPago: new FormControl(),
            fechaDesde: new FormControl(),
            fechaHasta: new FormControl(),
            cliente: new FormControl(this.clienteCobro ? this.clienteCobro : ''),
            comprobante: new FormControl(''),
            numero: new FormControl(''),
            puntoVenta: new FormControl(''),
            tipoPago: new FormControl(0),
            comprobanteAsociado: new FormControl(''),
            ventaAsociada: new FormControl(''),
            articulo: new FormControl(''),
            cantidad: new FormControl(''),
            precioUnitario: new FormControl(''),
            total: new FormControl({
                value: '',
                disabled: true,
            }),
            detalle: new FormControl(''),
            observaciones: new FormControl(''),
        });

        this.initObservadores();

        this.spinner = false;
    }

    calcularImportes() {
        let cantidad = this.facturaForm.get('cantidad').value;
        let precioUnitario = this.facturaForm.get('precioUnitario').value;
        let total = cantidad * precioUnitario;

        this.facturaForm.get('total').setValue(total);

        if(total > this.faltaFacturarVista){
            this.alert.error("El total del articulo no puede ser mayor al monto a facturar")
        }
    }

    calcularPrecioUnitario() {
        const articuloSeleccionado = this.facturaForm.get('articulo').value;

        if (!articuloSeleccionado) {
            return this.alert.error('Seleccione un art√≠culo v√°lido');
        }

        this.dialog
            .open(CalcularUnitarioComponent, {
                data: articuloSeleccionado,
                panelClass: 'ayuda-dialog',
            })
            .afterClosed()
            .subscribe({
                next: (res) => {
                    if (res.success == true) {
                        this.facturaForm
                            .get('precioUnitario')
                            .setValue(res.result);
                    }
                },
            });
    }

    displayFn(cliente): string {
        return cliente && cliente.razonSocial ? cliente.razonSocial : '';
    }
    displayFnArticulos(articulo): string {
        return articulo && articulo.descripcion ? articulo.descripcion : '';
    }

    getArticulos(value?: string) {
        let filter: string = `?sort=descripcion&pagination=1&sort=-total_stock&per-page=10&page=0&filter[tipo][in][]=1&filter[tipo][in][]=2`;
        let filterBusqueda: string = `&filter[descripcion][like]=${value}`;
        this.facturacionService.getArticulos(filter, filterBusqueda).subscribe({
            next: (res: any) => {
                this.articulos = res.result;
            },
        });
    }

    getClientes(value?) {
        let filter: string = value ? `?filter[razonSocial][like]=${value}` : '';
        this.facturacionService.getClientes(filter).subscribe({
            next: (res: any) => {
                this.clientes = res.result;
                this.clienteCobro = this.clientes.filter((cliente: any) => cliente.idcliente == this.idcliente);
                if(this.cobro){
                    this.getVentasAsociadas();
                }
            },
        });
    }

    getDatosEmpresa() {
        this.facturacionService.getDatosEmpresa().subscribe({
            next: (res: any) => {
                if (res.success == true) {
                    this.comprobantes = res.result.comprobantes;
                    let comprobantesValidos = ['factura', 'debito', 'credito'];
                    this.comprobantes = this.comprobantes.filter(
                        (comprobante: any) =>
                            comprobantesValidos.some((valid: string) =>
                                comprobante.Desc.toLowerCase()
                                    .normalize('NFD')
                                    .replace(/[\u0300-\u036f]/g, '')
                                    .split(' ')
                                    .includes(valid)
                            )
                    );
                    this.puntosVenta = res.result.puntosVenta;
                } else {
                    this.alert.error(res.mensaje);
                }
            },
        });
    }

    getVentasAsociadas() {
        let filter: string = `?filter[idcliente]=${this.idcliente}`;
        this.facturacionService.getVentasAsociadas(filter).subscribe({
            next: (res: any) => {
                this.ventasAsociadas = res.result;
            },
        });
    }

    getComprobantesAsociados(value) {
        let filter: string = value;
        this.facturacionService.getComprobantesAsociados(filter).subscribe({
            next: (res: any) => {
                this.comprobantesAsociados = res.result;
            },
        });
    }

    getDatosCobro() {
        this.spinner = true;
        return new Promise((resolve, reject) => {
            let filter = `?filter[idcobro]=${this.idCobro}`;
            this.facturacionService.getDatosCobro(filter).subscribe({
                next: (res: any) => {
                    this.cobro = res.result[0];
                    this.idcliente = this.cobro.idcliente;
                    this.faltaFacturarVista = Number(this.cobro.a_facturar); //Para mostrar en la vista
                    this.montoFaltaFacturar = Number(this.cobro.a_facturar); //Para calculos
                    resolve(true);
                },
            });
        });
    }

    goBack() {
        this.router.navigate(['/cobranzas']);
    }

    formatDate(date: Date) {
        let fecha = new Date(date);
        let seconds = fecha.getSeconds().toString().padStart(2, '0');
        let minutes = fecha.getMinutes().toString().padStart(2, '0');
        let hours = fecha.getHours().toString().padStart(2, '0');
        let day = fecha.getDate().toString().padStart(2, '0');
        let month = (fecha.getMonth() + 1).toString().padStart(2, '0');
        let year = fecha.getFullYear();
        return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
    }

    removeArticulo(articulo) {
        this.subtotal = this.subtotal - articulo.total;
        this.montoFaltaFacturar = this.montoFaltaFacturar + articulo.total;

        if(this.cobro){
            this.faltaFacturarVista =
            Math.round(this.montoFaltaFacturar * 100) / 100;
            if (this.faltaFacturarVista == 0) {
                this.facturaForm.get('articulo').disable();
                this.facturaForm.get('cantidad').disable();
                this.facturaForm.get('precioUnitario').disable();
                this.facturaForm.get('detalle').disable();
            } else {
                this.facturaForm.get('articulo').enable();
                this.facturaForm.get('cantidad').enable();
                this.facturaForm.get('precioUnitario').enable();
                this.facturaForm.get('detalle').enable();
            }
        }

        this.facturaDataSource.data = this.facturaDataSource.data.filter(
            (item) => item.idarticulo != articulo.idarticulo
        );

        if (this.facturaDataSource.data.length == 0) {
            this.facturaDataSource.data = [
                {
                    idarticulo: '',
                    cantidad: '',
                    importe_unitario: '',
                    detalle: '',
                },
                {
                    idarticulo: '',
                    cantidad: '',
                    importe_unitario: '',
                    detalle: '',
                },
                {
                    idarticulo: '',
                    cantidad: '',
                    importe_unitario: '',
                    detalle: '',
                },
            ];
        }

    }

    addArticulo() {
        let articulo = this.facturaForm.get('articulo').value;
        let cantidad = this.facturaForm.get('cantidad').value;
        let precioUnitario = this.facturaForm.get('precioUnitario').value;
        let detalle = `${articulo.codigo_interno} - ${
            this.facturaForm.get('detalle').value || articulo.descripcion
        }`;
        let totalArticulo = cantidad * precioUnitario;

        if (
            this.facturaDataSource.data.some(
                (itemTabla) => itemTabla.idarticulo == articulo.idarticulo
            )
        ) {
            this.alert.error('No puede agregar el mismo articulo');
        } else {
            if (totalArticulo > this.faltaFacturarVista) {
                this.alert.error(
                    'El total del art√≠culo no puede ser mayor al monto a facturar'
                );
            } else {
                if (
                    articulo &&
                    cantidad &&
                    precioUnitario &&
                    detalle &&
                    totalArticulo
                ) {
                    this.facturaDataSource.data =
                        this.facturaDataSource.data.filter(
                            (articulo) => articulo.idarticulo != ''
                        );
                    let itemTabla = {
                        idarticulo: articulo.idarticulo,
                        cantidad: cantidad,
                        importe_unitario: precioUnitario,
                        detalle: detalle,
                        total: totalArticulo,
                    };

                    this.facturaDataSource.data = [
                        ...this.facturaDataSource.data,
                        itemTabla,
                    ];

                    this.subtotal = this.facturaDataSource.data.reduce(
                        (acc, item) => acc + item.total,
                        0
                    );

                    this.facturaForm.get('total').setValue('');
                    this.facturaForm.get('articulo').setValue('');
                    this.facturaForm.get('cantidad').setValue('');
                    this.facturaForm.get('precioUnitario').setValue('');
                    this.facturaForm.get('detalle').setValue('');
                    if(this.cobro){
                        this.montoFaltaFacturar =
                            this.montoFaltaFacturar - totalArticulo;
    
                        this.faltaFacturarVista =
                            Math.round(this.montoFaltaFacturar * 100) / 100;
                            if (this.faltaFacturarVista == 0) {
                                this.facturaForm.get('articulo').disable();
                                this.facturaForm.get('cantidad').disable();
                                this.facturaForm.get('precioUnitario').disable();
                                this.facturaForm.get('detalle').disable();
                            } else {
                                this.facturaForm.get('articulo').enable();
                                this.facturaForm.get('cantidad').enable();
                                this.facturaForm.get('precioUnitario').enable();
                                this.facturaForm.get('detalle').enable();
                            }
                    }
                } else {
                    this.alert.error('Revise los campos del art√≠culo');
                }
            }
        }
    }

    onSubmit() {
        let fechaEmision = this.formatDate(this.facturaForm.get('fecha').value);
        let fechaVencimientoPago = this.formatDate(
            this.facturaForm.get('vencimientoPago').value
        );
        let fechaDesde = this.formatDate(
            this.facturaForm.get('fechaDesde').value
        );
        let fechaHasta = this.formatDate(
            this.facturaForm.get('fechaHasta').value
        );

        if (this.facturaForm.valid) {
            let payload = {
                factura: {
                    fechaEmision: fechaEmision,
                    fechaVencimientoPago: fechaVencimientoPago,
                    cliente: this.facturaForm.get('cliente').value.codigo,
                    tipoComprobante:
                        this.facturaForm.get('comprobante').value.Id,
                    puntoVenta: this.facturaForm
                        .get('puntoVenta')
                        .value.toString(),
                    fechaServicioDesde: fechaDesde,
                    fechaServicioHasta: fechaHasta,
                    contado: this.facturaForm.get('tipoPago').value,
                    comprobante_asociado:
                        this.facturaForm.get('comprobanteAsociado').value.codigo ||
                        null,
                    observaciones:
                        this.facturaForm.get('observaciones').value || null,
                    //idventa: 232, // opcional
                    //venta: null, // opcional
                    //idventapago: null, // opcional
                    //idcobroformapago: null, // opcional
                },
                items: this.facturaDataSource.data,
            };

            this.facturacionService.createFactura(payload).subscribe({
                next: (res: any) => {
                    if(res.success = true){
                        this.router.navigate(['/facturas'])
                    }
                },
            });
        } else {
        }
    }

    ngOnDestroy(): void {}
}
